import { Injectable } from '@angular/core';
import { HttpClient,HttpHeaders} from '@angular/common/http';



@Injectable({
  providedIn: 'root'
})
export class AuthserviceService {
url="http://localhost:8585/dashboard/"
 
 token = localStorage.getItem('token')

 headers = new HttpHeaders({
  'Content-Type': 'application/json',
  'Authorization': `Bearer ${this.token}`
});
 options = {
  headers: this.headers
}
  constructor(
    private http: HttpClient,

  ) { }

  registration(urseData){
    return this.http.post(this.url+`registration`,urseData)
  }

  loginData(urseData){
    return this.http.post(this.url+`login`,urseData)
  }
  loginTeacherData(id){ 
    return this.http.post(this.url+`teacherInfobyID/${id}`,this.options)
  }

  addCoures(id){ 
    console.log('id :>> ', id);
    // return this.http.post(this.url+`teacherInfobyID/${id}`,this.options)
  }
 // {queryParams: { teacherId: id}}/,
}
